section {
  width: 70vw;
  min-height: 50vw;
  padding: 20px 20px;
  margin: auto;
  border: 3px solid black;
  margin-top: 100px;
  border-radius: 20px;
  h1 {
    text-align: center;
    margin-bottom: 50px;
  }
  .form {
    margin: auto;
    width: 80%;
    height: 40px;
    background-color: white;
    border-radius: 10px;
    margin-bottom: 50px;

    form {
      width: 90%;
      height: 100%;
      margin: auto;

      input {
        width: 85%;
        height: 100%;
        padding: 0 10px;
        border-radius: 5px;
        background-color: #e6e6e6;
        border: none;
        outline: none;
      }
      button {
        width: 15%;
        height: 100%;
        border-radius: 5px;
        border: none;
      }
    }
  }
  .todos {
    margin: auto;
    width: 70%;
    height: 100%;
    .todo {
      border-radius: 10px;
      margin-bottom: 15px;
      box-sizing: border-box;
      justify-content: space-between;
      width: 100%;
      height: 50px;
      overflow: hidden;
      display: flex;
      .tip {
        flex: 1;
        background-color: teal;
      }
      .second {
        flex: 10;
        display: flex;
        justify-content: space-between;
        background-color: #e6e6e6;
        span {
          padding-left: 10px;
          width: auto;
          height: 100%;
          display: flex;
          align-items: center;
        }
        .btn {
          width: 130px;
          display: flex;
          align-items: center;
          justify-content: space-around;
        }
      }
      &.complete {
        .second {
          span {
            text-decoration: line-through;
            font-style: italic;
          }
        }
      }
    }
  }
}
